// src/pages/MainPage.jsx
import { Box, Button, Container, Heading, Text, SimpleGrid, Image, HStack, Link } from "@chakra-ui/react";
import { Link as RouterLink } from "react-router-dom";
import { FaGithub, FaEnvelope } from "react-icons/fa";
import posts from "../posts.json";

export default function MainPage() {
  //   
  const projects = [
    {
      title: "()",
      description: "   ,  ",
      image: "https://picsum.photos/600/400?random=1",
      link: "https://github.com/rlaxoehd4234/chulgunhaza-backend",
    },
    {
      title: "( )",
      description: "      ",
      image: "https://private-user-images.githubusercontent.com/92311926/290033658-dbd99772-9369-4e6d-98a3-ad4b7373b1af.png?jwt=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..FVZ3CaMYK_aa59qf17Y4tFTzvZQtKE7YLLnxZX9Kxa0",
      link: "https://github.com/rlaxoehd4234/douzoneCorrectiveAction",
    },
    {
      title: "Umbrella-  ",
      description: "Notion, Discord       ",
      image: "https://private-user-images.githubusercontent.com/92311926/397373052-3ae04c6c-ee6b-49af-8e4d-b20e884f1e7d.png?jwt=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..bJBKCKAmiK9Qgq2EXJmXoFfvRZjM8E_Tl_iyTKcXFJg",
      link: "https://github.com/rlaxoehd4234/Umbrella",
    },
  ];

  return (
    <>
      {/* Home + About  */}
      <section id="home" style={{ minHeight: "50vh", padding: "2rem" , background: "#f9fafb"  }}>
        {/*  */}
        <Container maxW="5xl" textAlign="center" position="relative" zIndex={1}>
            <Heading size="2xl" mb={4}>, </Heading>
            <Text fontSize="lg" mb={4}>
               .
            </Text>
            <Text fontSize="md" mb={10} opacity={0.9}>
            React, Spring, MSA   ,
                    .
            </Text>

            <HStack spacing={4} justify="center">
            <Button as={Link} href="https://github.com/rlaxoehd4234" target="_blank" colorScheme="blackAlpha">
                GitHub
            </Button>
            <Button as={Link} href="mailto:rlaxoehd4234.com" colorScheme="purple">
                Email
            </Button>
            <Button as={RouterLink} to="/about" colorScheme="yellow" size="lg">
                About Me
            </Button>
            </HStack>
        </Container>
        </section>


      {/* Blog  */}
      <section id="blog" style={{ minHeight: "50vh", padding: "2rem" , background: "#f9fafb"  }}>
        <Container maxW="6xl">
          <Heading size="xl" textAlign="center" mb={8}>  </Heading>
          <SimpleGrid columns={[1, 2, 3]} spacing={6}>
            {posts.slice(0, 3).map((post) => (
              <Box
                key={post.slug}
                shadow="md"
                borderWidth="1px"
                rounded="lg"
                bg="white"
                overflow="hidden"
                _hover={{ shadow: "xl", transform: "translateY(-4px)" }}
                transition="all 0.2s"
              >
                <Image
                  src={post.image || "https://via.placeholder.com/600x400"}
                  alt={post.title}
                  w="100%"
                  h="180px"
                  objectFit="cover"
                />
                <Box p={5}>
                  <Heading size="md" mb={2} color="green.600" noOfLines={1}>
                    <RouterLink to={`/blog/${post.slug}`}>{post.title}</RouterLink>
                  </Heading>
                  <Text fontSize="sm" color="gray.500">
                     {post.author}  {post.date}
                  </Text>
                </Box>
              </Box>
            ))}
          </SimpleGrid>
          <Box textAlign="center" mt={8}>
            <Button as={RouterLink} to="/blog" colorScheme="green">
                
            </Button>
          </Box>
        </Container>
      </section>

      {/* Projects  */}
      <section id="projects" style={{ minHeight: "60vh", padding: "2rem", background: "#f9fafb" }}>
        <Container maxW="6xl">
          <Heading size="xl" textAlign="center" mb={8}></Heading>
          <SimpleGrid columns={[1, 2, 3]} spacing={6}>
            {projects.map((project) => (
              <Box
                key={project.title}
                shadow="md"
                borderWidth="1px"
                rounded="lg"
                bg="white"
                overflow="hidden"
                _hover={{ shadow: "xl", transform: "translateY(-4px)" }}
                transition="all 0.2s"
              >
                <Image
                  src={project.image}
                  alt={project.title}
                  w="100%"
                  h="180px"
                  objectFit="cover"
                />
                <Box p={5}>
                  <Heading size="md" mb={2} color="purple.600" noOfLines={1}>
                    {project.title}
                  </Heading>
                  <Text fontSize="sm" color="gray.600" mb={4}>
                    {project.description}
                  </Text>
                  <Button
                    as={Link}
                    href={project.link}
                    target="_blank"
                    colorScheme="purple"
                    size="sm"
                  >
                     
                  </Button>
                </Box>
              </Box>
            ))}
          </SimpleGrid>
        </Container>
      </section>
    </>
  );
}
